import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

/**
 * Formata data e hora para o padrão brasileiro
 */
export function formatDateTimeBR(dateString: string): string {
  return new Date(dateString).toLocaleString('pt-BR', {
    timeZone: 'America/Sao_Paulo',
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  })
}

/**
 * Formata apenas a data para o padrão brasileiro
 */
export function formatDateBR(dateString: string): string {
  return new Date(dateString).toLocaleDateString('pt-BR', {
    timeZone: 'America/Sao_Paulo',
    day: '2-digit',
    month: '2-digit',
    year: 'numeric'
  })
}

/**
 * Formata apenas a hora para o padrão brasileiro
 */
export function formatTimeBR(dateString: string): string {
  return new Date(dateString).toLocaleTimeString('pt-BR', {
    timeZone: 'America/Sao_Paulo',
    hour: '2-digit',
    minute: '2-digit'
  })
}

/**
 * Calcula a diferença de tempo em formato legível
 */
export function getTimeAgo(dateString: string): string {
  const now = new Date()
  const date = new Date(dateString)
  const diffInMs = now.getTime() - date.getTime()
  const diffInMinutes = Math.floor(diffInMs / (1000 * 60))
  const diffInHours = Math.floor(diffInMinutes / 60)
  const diffInDays = Math.floor(diffInHours / 24)

  if (diffInMinutes < 1) return 'Agora mesmo'
  if (diffInMinutes < 60) return `${diffInMinutes} min atrás`
  if (diffInHours < 24) return `${diffInHours}h atrás`
  if (diffInDays < 7) return `${diffInDays} dias atrás`
  
  return formatDateBR(dateString)
}

/**
 * Calcula tempo restante para deadline de SLA
 */
export function getSLATimeRemaining(deadlineString: string): {
  text: string
  status: 'ok' | 'warning' | 'critical' | 'overdue'
  percentage: number
} {
  const now = new Date()
  const deadline = new Date(deadlineString)
  const diffInMs = deadline.getTime() - now.getTime()
  const diffInHours = diffInMs / (1000 * 60 * 60)

  if (diffInMs <= 0) {
    return {
      text: 'Vencido',
      status: 'overdue',
      percentage: 0
    }
  }

  if (diffInHours < 1) {
    const minutes = Math.floor(diffInMs / (1000 * 60))
    return {
      text: `${minutes} min restantes`,
      status: 'critical',
      percentage: Math.max(0, (diffInHours / 24) * 100)
    }
  }

  if (diffInHours < 4) {
    return {
      text: `${Math.floor(diffInHours)}h restantes`,
      status: 'critical',
      percentage: Math.max(0, (diffInHours / 24) * 100)
    }
  }

  if (diffInHours < 12) {
    return {
      text: `${Math.floor(diffInHours)}h restantes`,
      status: 'warning',
      percentage: (diffInHours / 24) * 100
    }
  }

  const days = Math.floor(diffInHours / 24)
  const hours = Math.floor(diffInHours % 24)
  
  return {
    text: days > 0 ? `${days}d ${hours}h restantes` : `${hours}h restantes`,
    status: 'ok',
    percentage: Math.min(100, (diffInHours / 72) * 100) // Assumindo SLA padrão de 72h
  }
}

/**
 * Formata valores monetários em Real brasileiro
 */
export function formatCurrencyBR(value: number): string {
  return new Intl.NumberFormat('pt-BR', {
    style: 'currency',
    currency: 'BRL'
  }).format(value)
}

/**
 * Formata números com separadores brasileiros
 */
export function formatNumberBR(value: number): string {
  return new Intl.NumberFormat('pt-BR').format(value)
}

/**
 * Valida CPF brasileiro
 */
export function validateCPF(cpf: string): boolean {
  cpf = cpf.replace(/[^\d]/g, '')
  
  if (cpf.length !== 11 || /^(\d)\1{10}$/.test(cpf)) {
    return false
  }

  let sum = 0
  for (let i = 0; i < 9; i++) {
    sum += parseInt(cpf.charAt(i)) * (10 - i)
  }
  
  let remainder = (sum * 10) % 11
  if (remainder === 10 || remainder === 11) remainder = 0
  if (remainder !== parseInt(cpf.charAt(9))) return false

  sum = 0
  for (let i = 0; i < 10; i++) {
    sum += parseInt(cpf.charAt(i)) * (11 - i)
  }
  
  remainder = (sum * 10) % 11
  if (remainder === 10 || remainder === 11) remainder = 0
  
  return remainder === parseInt(cpf.charAt(10))
}

/**
 * Valida CNPJ brasileiro
 */
export function validateCNPJ(cnpj: string): boolean {
  cnpj = cnpj.replace(/[^\d]/g, '')
  
  if (cnpj.length !== 14 || /^(\d)\1{13}$/.test(cnpj)) {
    return false
  }

  const weights1 = [5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2]
  const weights2 = [6, 5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2]

  let sum = 0
  for (let i = 0; i < 12; i++) {
    sum += parseInt(cnpj.charAt(i)) * weights1[i]
  }
  
  let remainder = sum % 11
  const digit1 = remainder < 2 ? 0 : 11 - remainder

  if (digit1 !== parseInt(cnpj.charAt(12))) return false

  sum = 0
  for (let i = 0; i < 13; i++) {
    sum += parseInt(cnpj.charAt(i)) * weights2[i]
  }
  
  remainder = sum % 11
  const digit2 = remainder < 2 ? 0 : 11 - remainder
  
  return digit2 === parseInt(cnpj.charAt(13))
}

/**
 * Formata CPF com máscara
 */
export function formatCPF(cpf: string): string {
  cpf = cpf.replace(/[^\d]/g, '')
  return cpf.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')
}

/**
 * Formata CNPJ com máscara
 */
export function formatCNPJ(cnpj: string): string {
  cnpj = cnpj.replace(/[^\d]/g, '')
  return cnpj.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/, '$1.$2.$3/$4-$5')
}

/**
 * Formata telefone brasileiro
 */
export function formatPhone(phone: string): string {
  phone = phone.replace(/[^\d]/g, '')
  
  if (phone.length === 10) {
    return phone.replace(/(\d{2})(\d{4})(\d{4})/, '($1) $2-$3')
  } else if (phone.length === 11) {
    return phone.replace(/(\d{2})(\d{5})(\d{4})/, '($1) $2-$3')
  }
  
  return phone
}

/**
 * Gera ID único para tickets
 */
export function generateTicketId(prefix: string = 'SD'): string {
  const year = new Date().getFullYear()
  const timestamp = Date.now().toString().slice(-6)
  const random = Math.floor(Math.random() * 1000).toString().padStart(3, '0')
  
  return `${prefix}-${year}-${timestamp}${random}`
}

/**
 * Calcula prioridade baseada em SLA e categoria
 */
export function calculatePriority(category: string, createdAt: string): 'Baixa' | 'Média' | 'Alta' | 'Urgente' {
  const now = new Date()
  const created = new Date(createdAt)
  const hoursElapsed = (now.getTime() - created.getTime()) / (1000 * 60 * 60)

  // Regras específicas por categoria
  switch (category) {
    case 'Peças Betim/Goiana/Porto Real':
      if (hoursElapsed > 20) return 'Urgente'
      if (hoursElapsed > 12) return 'Alta'
      return 'Média'
      
    case 'Garantia Brasil':
      if (hoursElapsed > 60) return 'Alta'
      if (hoursElapsed > 24) return 'Média'
      return 'Baixa'
      
    case 'Suporte Técnico':
      if (hoursElapsed > 40) return 'Alta'
      if (hoursElapsed > 20) return 'Média'
      return 'Baixa'
      
    default:
      if (hoursElapsed > 96) return 'Média'
      return 'Baixa'
  }
}

/**
 * Verifica se é horário comercial brasileiro
 */
export function isBusinessHours(date: Date = new Date()): boolean {
  const brazilTime = new Date(date.toLocaleString("en-US", {timeZone: "America/Sao_Paulo"}))
  const day = brazilTime.getDay() // 0 = domingo, 6 = sábado
  const hour = brazilTime.getHours()
  
  // Segunda a sexta, 8h às 18h
  return day >= 1 && day <= 5 && hour >= 8 && hour < 18
}

/**
 * Lista de feriados brasileiros para o ano atual
 */
export function getBrazilianHolidays(year: number = new Date().getFullYear()): Date[] {
  const holidays: Date[] = [
    new Date(year, 0, 1),   // Confraternização Universal
    new Date(year, 3, 21),  // Tiradentes
    new Date(year, 4, 1),   // Dia do Trabalhador
    new Date(year, 8, 7),   // Independência do Brasil
    new Date(year, 9, 12),  // Nossa Senhora Aparecida
    new Date(year, 10, 2),  // Finados
    new Date(year, 10, 15), // Proclamação da República
    new Date(year, 11, 25)  // Natal
  ]
  
  // Adicionar feriados móveis (Carnaval, Sexta-feira Santa) seria mais complexo
  // e requereria cálculo baseado na Páscoa
  
  return holidays
}

/**
 * Verifica se uma data é feriado brasileiro
 */
export function isBrazilianHoliday(date: Date): boolean {
  const holidays = getBrazilianHolidays(date.getFullYear())
  return holidays.some(holiday => 
    holiday.getDate() === date.getDate() && 
    holiday.getMonth() === date.getMonth()
  )
}
