'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Progress } from '@/components/ui/progress'

interface TicketStats {
  total: number
  open: number
  inProgress: number
  resolved: number
  slaCompliance: number
}

interface Ticket {
  id: string
  title: string
  category: string
  priority: 'Alta' | 'Média' | 'Baixa'
  status: 'Aberto' | 'Em Andamento' | 'Resolvido'
  createdAt: string
  slaDeadline: string
  dealership: string
  brand: 'Peugeot' | 'Citroën' | 'Fiat'
}

const mockStats: TicketStats = {
  total: 1247,
  open: 89,
  inProgress: 156,
  resolved: 1002,
  slaCompliance: 94.2
}

const mockTickets: Ticket[] = [
  {
    id: 'SD-2024-001',
    title: 'Peça indisponível - Filtro de óleo Peugeot 208',
    category: 'Peças Betim/Goiana/Porto Real',
    priority: 'Alta',
    status: 'Em Andamento',
    createdAt: '2024-01-15T09:30:00Z',
    slaDeadline: '2024-01-15T13:30:00Z',
    dealership: 'Peugeot São Paulo Centro',
    brand: 'Peugeot'
  },
  {
    id: 'SD-2024-002',
    title: 'Solicitação de garantia - Motor Fiat Argo',
    category: 'Garantia Brasil',
    priority: 'Média',
    status: 'Aberto',
    createdAt: '2024-01-15T10:15:00Z',
    slaDeadline: '2024-01-15T14:15:00Z',
    dealership: 'Fiat Belo Horizonte',
    brand: 'Fiat'
  },
  {
    id: 'SD-2024-003',
    title: 'Dúvida técnica - Sistema de injeção C4 Cactus',
    category: 'Suporte Técnico',
    priority: 'Média',
    status: 'Resolvido',
    createdAt: '2024-01-14T16:45:00Z',
    slaDeadline: '2024-01-16T16:45:00Z',
    dealership: 'Citroën Rio de Janeiro',
    brand: 'Citroën'
  }
]

const categories = [
  'Peças Betim/Goiana/Porto Real',
  'Garantia Brasil',
  'Suporte Técnico',
  'Campanhas & Incentivos',
  'Marketing Co-Op'
]

export default function ServiceDeskDashboard() {
  const [selectedCategory, setSelectedCategory] = useState('todos')
  const [tickets, setTickets] = useState<Ticket[]>(mockTickets)
  const [stats, setStats] = useState<TicketStats>(mockStats)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'Alta': return 'bg-red-100 text-red-800'
      case 'Média': return 'bg-yellow-100 text-yellow-800'
      case 'Baixa': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Aberto': return 'bg-blue-100 text-blue-800'
      case 'Em Andamento': return 'bg-orange-100 text-orange-800'
      case 'Resolvido': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getBrandColor = (brand: string) => {
    switch (brand) {
      case 'Peugeot': return 'bg-blue-100 text-blue-800'
      case 'Citroën': return 'bg-red-100 text-red-800'
      case 'Fiat': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const filteredTickets = selectedCategory === 'todos' 
    ? tickets 
    : tickets.filter(ticket => ticket.category === selectedCategory)

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('pt-BR', {
      timeZone: 'America/Sao_Paulo',
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard Service Desk</h1>
          <p className="text-gray-600 mt-1">
            Gestão unificada de atendimento - Rede Nacional
          </p>
        </div>
        <div className="flex space-x-2">
          <Button variant="outline" size="sm">
            Configurações
          </Button>
          <Button size="sm">
            Novo Ticket
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total de Tickets</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-gray-900">{stats.total.toLocaleString('pt-BR')}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Abertos</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{stats.open}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Em Andamento</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{stats.inProgress}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Resolvidos</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.resolved}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">SLA Compliance</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.slaCompliance}%</div>
            <Progress value={stats.slaCompliance} className="mt-2" />
          </CardContent>
        </Card>
      </div>

      {/* Error Alert */}
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Main Content */}
      <Tabs value={selectedCategory} onValueChange={setSelectedCategory} className="space-y-4">
        <TabsList className="grid w-full grid-cols-6">
          <TabsTrigger value="todos">Todos</TabsTrigger>
          {categories.map((category) => (
            <TabsTrigger key={category} value={category} className="text-xs">
              {category.split(' ')[0]}
            </TabsTrigger>
          ))}
        </TabsList>

        <TabsContent value={selectedCategory} className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>
                {selectedCategory === 'todos' ? 'Todos os Tickets' : selectedCategory}
              </CardTitle>
              <CardDescription>
                {filteredTickets.length} ticket(s) encontrado(s)
              </CardDescription>
            </CardHeader>
            <CardContent>
              {loading ? (
                <div className="flex items-center justify-center py-8">
                  <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                </div>
              ) : (
                <div className="space-y-4">
                  {filteredTickets.map((ticket) => (
                    <div key={ticket.id} className="border rounded-lg p-4 hover:bg-gray-50 transition-colors">
                      <div className="flex justify-between items-start mb-2">
                        <div className="flex-1">
                          <div className="flex items-center space-x-2 mb-1">
                            <span className="font-medium text-gray-900">{ticket.id}</span>
                            <Badge className={getBrandColor(ticket.brand)}>
                              {ticket.brand}
                            </Badge>
                            <Badge className={getPriorityColor(ticket.priority)}>
                              {ticket.priority}
                            </Badge>
                            <Badge className={getStatusColor(ticket.status)}>
                              {ticket.status}
                            </Badge>
                          </div>
                          <h3 className="font-medium text-gray-900 mb-1">{ticket.title}</h3>
                          <p className="text-sm text-gray-600">{ticket.dealership}</p>
                        </div>
                        <div className="text-right text-sm text-gray-500">
                          <div>Criado: {formatDate(ticket.createdAt)}</div>
                          <div>SLA: {formatDate(ticket.slaDeadline)}</div>
                        </div>
                      </div>
                      <div className="flex justify-between items-center">
                        <span className="text-sm text-gray-600">{ticket.category}</span>
                        <Button variant="outline" size="sm">
                          Ver Detalhes
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
